<template>
  <div>
    <el-container>
      <el-header>
        <a name="top"></a>广东电信故障工单周度管控通报
      </el-header>
      <span style="text-align:center;color:rgb(21,82,179);font-weight:bold">
              △遗留跟踪单
              △超长历时在途工单
            <hr style="height:1px; background-color:rgb(21,82,179);border:none"/>
            </span>
      <div class="time-filter" style="width: 600px; margin: 0 auto">
        <div class="block" style="margin: 0 auto">
          <el-form ref="form"  :model="form" label-width="80px">
            <el-row :gutter="200">
              <el-col :span="11">
                <el-form-item>
                  <el-date-picker
                    v-model="valueDate"
                    type="daterange"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    value-format="yyyy-MM-dd"
                    :default-time="['00:00:00', '23:59:59']">
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="3">
                <el-form-item>
                  <el-button type="primary" @click="DataFilter()" >提交</el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </div>
      <el-main>
        <div class="main">
          <h3>一、遗留跟踪单</h3>
          <el-row>
      <el-col :span="24">
        <template>
          <el-table
            :data="weekLeaveData"
            style="width: 100%"
            row-style="height:0"
            cell-style="padding:5px"
            border>
            <el-table-column
              prop="city"
              label="处理部门"
              width="100"
              align="center">
            </el-table-column>
            <el-table-column
              prop="sum_amount"
              label="在途遗留跟踪单（张）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="new_amount"
              label="本周新增在途（张）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="new_rate"
              label="本周新增占比（%）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="finish_amount"
              label="本周完成（张）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="finish_rate"
              label="本周完成占比（%）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="over1_amount"
              label="处理时间超过1个月（张）"
              minwidth
              align="center">
            </el-table-column>
            <el-table-column
              prop="over3_amount"
              label="处理时间超过3个月（张）"
              minwidth
              align="center">
            </el-table-column>
          </el-table>
        </template>
      </el-col>
      </el-row>
          <el-row>
       <el-col :span="24">
      <div class="proportion">
       <div id="chart_LeaveProportion" style="height: 400px; border: 1px solid"></div>
       </div>
       </el-col>
      </el-row>
          <h3>二、超长历时在途工单</h3>
          <el-row>
            <el-col :span="12">
              <template>
                <el-upload
                  class="upload-demo"
                  ref="upload"
                  action="http://132.96.194.30:8078/report/upload/"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :file-list="fileList"
                  :auto-upload="false">
                  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
                  <span slot="tip" class="el-upload__tip">只能上传xls文件</span>
                </el-upload>
              </template>
            </el-col>
          </el-row>
          <br>
          <el-row>
            <el-col :span="24">
                <template>
                  <el-table
                    :data="weekOverlongData"
                    style="width: 100%"
                    row-style="height:0"
                    cell-style="padding:5px"
                    border>
                    <el-table-column
                      prop="city"
                      label="工单停留单位"
                      width="110"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="sum_amount"
                      label="在途网络单总数（张）"
                      width="180px"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="amount_3_7"
                      label="故障历时：72-168小时（张）"
                      minwidth
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="amount_3_7_rate"
                      label="占比（%）"
                      width="100px"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="amount_over_7"
                      label="故障历时：>168小时（张）"
                      minwidth
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="amount_over_7_rate"
                      label="占比（%）"
                      width="100px"
                      align="center">
                    </el-table-column>
                  </el-table>
                </template>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <div class="proportion">
                <div id="chart_OverLongProportion" style="height: 400px; border: 1px solid"></div>
              </div>
            </el-col>
          </el-row>
 </div>
 </el-main>
</el-container>
</div>
</template>

<script>
export default {
  data () {
    return {
      weekOverlongData: [],
      weekLeaveData: [],
      valueWeek: '',
      valueDate: ['2018-01-01', '2018-01-07'],
      fileList: []
    }
  },
  computed: {
    myBegin_datetime () {
      return this.valueDate[0]
    },
    myEnd_datetime () {
      return this.valueDate[1]
    }
  },
  methods: {
    table_weekOverlong () { // 表1
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/longtime/'
      }).then(response => {
        if (response.data.status) {
          this.weekOverlongData = response.data.result
        }
      }).catch(error => {
        console.log(error)
      })
    },
    draw_weekOverlong () { // 图1
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/longtime/'
      }).then(response => {
        var cityList = []
        var CompletionRatioValueList = []
        var data = response.data.result
        var l = response.data.result.length
        for (var i = 0; i < l; i++) {
          cityList.push(data[i]['city'])
          CompletionRatioValueList.push(data[i]['amount_over_7_rate'])
        }
        let myChart = this.$echarts.init(document.getElementById('chart_OverLongProportion'))
        myChart.setOption({
          color: ['#5c9ad5'],
          title: {
            text: '占比'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: cityList
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, 0.01],
            axisLabel: {
              show: true,
              interval: 'auto',
              formatter: '{value} %'}
          },
          xAxis: {
            type: 'category',
            axisLabel: {
              interval: 0 //  横轴信息全部显示
            },
            data: cityList
          },
          series: [
            {
              name: '本周完成占比',
              type: 'bar',
              barWidth: 20,
              data: CompletionRatioValueList
            }
          ]
        })
      })
    },
    table_weekLeave () { // 表2
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/track/' + this.myBegin_datetime + '/' + this.myEnd_datetime + '/'
      }).then(response => {
        if (response.data.status) {
          this.weekLeaveData = response.data.result
        }
      }).catch(error => {
        console.log(error)
      })
    },
    chart_weekLeave () { // 图2
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/track/' + this.myBegin_datetime + '/' + this.myEnd_datetime + '/'
      }).then(response => {
        var cityList = []
        var CompletionRatioValueList = []
        var data = response.data.result
        var l = response.data.result.length
        for (var i = 0; i < l; i++) {
          cityList.push(data[i]['city'])
          CompletionRatioValueList.push(data[i]['finish_rate'])
        }
        let myChart = this.$echarts.init(document.getElementById('chart_LeaveProportion'))
        myChart.setOption({
          color: ['#5c9ad5'],
          title: {
            text: '本周完成占比'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: cityList
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          yAxis: {
            type: 'value',
            // boundaryGap: [0, 0.01],
            axisLabel: {
              show: true,
              interval: 'auto',
              formatter: '{value} %'}
          },
          xAxis: {
            type: 'category',
            axisLabel: {
              interval: 0 //  横轴信息全部显示
            },
            data: cityList
          },
          series: [
            {
              name: '本周完成占比',
              type: 'bar',
              barWidth: 20,
              data: CompletionRatioValueList
            }
          ]
        })
      }
      )
    },
    DataFilter () {
      this.table_weekOverlong()
      this.draw_weekOverlong()
      this.table_weekLeave()
      this.chart_weekLeave()
    },
    submitUpload () {
      this.$refs.upload.submit()
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    }
  },
  mounted () {
    this.table_weekOverlong()
    this.table_weekLeave()
    this.draw_weekOverlong()
    this.chart_weekLeave()
  }
}
</script>

<style scoped>
  .el-header{
    height: 180px;
    color: rgb(110,110,110);
    text-align: center;
    line-height: 60px;
    font: 28px/60px bold 微软雅黑;
  }
  .main {
    margin: 0 auto;
    width: 90%;
  }
  .main h3{
    font: 18px/20px bold 微软雅黑;
    color: rgb(130,130,130);
    text-align:left;
  }
  /*.count{ width:1250px; border:1px; padding:2px; overflow:hidden ;margin: 10px 10px;}*/
  .proportion{
    margin-top: 30px;
  }
  .el-table .cell{
    line-height: 15px;
  }
  .el-form-item{
    margin-bottom: -13px;
  }
  .el-table thead.is-group th {
    background: #4473c5 !important;
  }
  .el-table thead,.el-table--border th, .el-table__fixed-right-patch,el-table__header{
    color: #ffffff;
    background-color: #4473c5 !important;
  }
  .el-table_2_column_10 is-center ,el-table_2_column_10 is-center is-leaf,el-table_1_column_1 is-center {
    background-color: #4473c5 !important;
    color: #ffffff !important;
  }
</style>



